@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding:wght@400;700&family=Nanum+Gothic:wght@400;700;800&family=Nanum+Pen+Script&display=swap');

@font-face {
    font-family: "nanum gothic light";
    src: url("../../../assets/fonts/나눔고딕L.woff") format('woff'), url("../../../assets/fonts/나눔고딕L.ttf") format('truetype');
    font-weight: normal;
    font-style: normal;
}


.backdrop{
    background-color: #EEEEEE;
    width: 100%;
    height: 100%;
}

.header{
    position: relative;
    background-color: #1C1C1C;
    display: flex;
    justify-content: center;
    align-items: center;
    flex:1;
    height: 85px;
}

.company-logo{
    background-image: URL('../../../assets/images/Statistic/logo_white.svg');
    height: 67%;
    aspect-ratio: 3 / 1;
    position: relative;
    background-size: contain;
    background-repeat: no-repeat;

}

.profile-bar{
    margin: 0px;
    position: relative;
    background-color: #ffffff;
    /* border: 1px solid #000000; */
    flex:1;
    height: 55px;
    display: flex;
    align-items: center;
}

.buttons-frame{
    position: absolute;
    height: 50%;
    aspect-ratio: 8 / 1;

    border: 1px dashed salmon;
}

.my-information-btn{
    position: absolute;
    width: 50%;
    height: 100%;
    left:0;
    font-size: 70%;
    background-color: hsla(0, 0%, 100%, 0);
    border: none;
    border-right: 1.5px solid #33A0C9;
    color: #33A0C9;
}

.log-out-btn{
    position: absolute;
    width: 50%;
    height: 100%;
    right:0;
    font-size: 70%;
    background-color: hsla(0, 0%, 100%, 0);
    border: none;
    color: #33A0C9;
}

.profile-name-frame{
    position: absolute;
    height: 77%;
    aspect-ratio: 1/1;
    border: 1px dashed salmon;
}

.image-circle{
    width: 100%;
    height: 100%;
    border-radius:50%;
    border: 1.5px solid #33A0C9;
}

.contents-container{
    width: 68%;
    height:fit-content;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    border: 1px solid #000000;
    background-color: #EEEEEE;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.image-container{
    position: relative;
    width:100%;
    aspect-ratio: 16 / 7;
    display: flex;
    align-items: center;
}

.header-image{
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("../../../assets/images/Statistic/field-sliced.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(118, 92, 92, 0.5);
    pointer-events: none;
}

.imsi{
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 35%;
    background-image: url("../../../assets/images/Statistic/imsi.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.link-slogan-container{
    font-family: "Nanum Gothic", sans-serif;
    font-weight: 700;
    font-style: bold;

    position: absolute;
    border: 2px solid #77c6cc;
    width : 80%;
    height: 57%;
    left: 8%;
    display: flex;
    flex-direction: row;
}

.slogan{
    position: absolute;
    top: 0%;
    font-size: 4.2vw;
    color: #ffffff;
    margin: 0;
    line-height: 130%;
}

.icon-link-box{
    border: 1px dashed salmon;
    position: absolute;
    bottom: 0%;
    height: 30%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}



.link-homePage{
    position: relative;
    font-family: "nanum gothic light", sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 3.0vw;
    color: #ffffff;
    bottom: 0%;
    margin: 0;
}

.link-icon {
    position: relative;
    background-image: url(../../../assets/images/Statistic/external-link.svg);
    height: 50%; /* 부모 요소에 따라 결정 */
    aspect-ratio: 1/1;
    background-size: contain;
    background-repeat: no-repeat;
}

.top-hr{
    position: relative;
    border : 0px;
    margin: 7%;
    border-top: 1.5px solid #5E5E5E;
    width: 90%
}

.test{
    position: relative;
    top:90px;
    width:100%;
    height: 160px;
    background-color: salmon;
}

.footer-box{
    position: absolute;
    width: 95%;
    aspect-ratio: 15/2;
    background-color: #1C1C1C;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    align-items: center;

}

.information-left{
    position: relative;
    height: 53%;
    width:fit-content;
    border: 1px dashed salmon;
    margin-left: 3%;
}

.information-right{
    position: absolute;
    height: 60%;
    right: 0%;
    width: 23%;
    border: 1px dashed salmon;
    margin-right: 3%;
    display: grid;
    grid-template-rows: 1.5fr 1fr 1fr 1fr;
    text-align: right;
}

.information{
    height: fit-content;
    right: 0%;
    margin: 0%;
    text-align: right;
}

.footer-logo{
    background-image: URL('../../../assets/images/Statistic/logo_white.svg');
    height: 33%;
    aspect-ratio: 3 / 1;
    position: relative;
    background-size: contain;
    background-repeat: no-repeat;
}


.Nanum-bold{
    position: relative;
    font-family: "Nanum Gothic", sans-serif;
    font-weight: 700;
    font-style: bold;
    color: #1C1C1C;
    margin: 0%;
}


.Nanum-light{
    position: relative;
    font-family: "nanum gothic light", sans-serif;
    font-weight: normal;
    font-style: normal;
    color: #1C1C1C;
}

.Nanum{
    margin: 0%;
    position: relative;
    font-family: "Nanum Gothic", sans-serif;
    font-weight: normal;
    font-style: normal;
    color: #1C1C1C;
}

.company-name{
    color:#7E99AE;
    margin-top: 4%;
    margin-bottom: 4%;
}

.contact{
    color: #28A3CD
}

.contact-way{
    color:#FFFFFF;
}

@media(max-width:1248px){
    .contents-container{
        width: 100%;
    }

    .slogan{
        font-size: 6.2vw;
    }

    .footer-box{
        width: 95%;
        aspect-ratio: 115/27;
    }
}